import React from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

function mapStateToPropsForRedirect(state) {
    return {
        isAuth: state.auth.isAuth,
    };
}

export default function withAuthRedirect(Component) {
    class RedirectComponent extends React.Component {
        render() {
            if (!this.props.isAuth) {
                return <Redirect to="/login" />;
            }
            return <Component {...this.props} />;
        }
    }

    const RedirectComponentContainer = connect(mapStateToPropsForRedirect)(RedirectComponent);

    return RedirectComponentContainer;
}
