import React from "react";
import "./Dialogs.css";
import DialogsItems from "./DialogsItems/DialogsItems";
import DialogsMessages from "./DialogsMessages/DialogsMessages";
import { Field, reduxForm } from "redux-form";
import { maxLengthCreater, required } from "../../../helper/validators";
import { TextArea } from "../common/FormsControl";

const maxLength5 = maxLengthCreater(5);

function DialogsForm(params) {
    return (
        <form action="#" className="mess__form" onSubmit={params.handleSubmit}>
            <Field className="mess__area" placeholder="your message...." component={TextArea} name="textarea" validate={[required, maxLength5]} />
            <button type="submit" className="mess__btn">
                Send
            </button>
        </form>
    );
}

const DialogsReduxForm = reduxForm({
    form: "dialogsMess",
})(DialogsForm);

export default function Dialogs(params) {
    function onSubmit(value) {
        if (value.textarea && value.textarea.length > 0) {
            params.addMessage(value.textarea);
        }
    }

    return (
        <div className="dialogs">
            <div className="title">Dialogs</div>
            <div className="row">
                <div className="users">
                    <DialogsItems data={params.usersData} />
                </div>
                <div className="body">
                    <div className="messages">
                        <DialogsMessages data={params.messagesData} />
                    </div>
                    <DialogsReduxForm onSubmit={onSubmit} />
                </div>
            </div>
        </div>
    );
}
